@import url('https://fonts.googleapis.com/css?family=Open+Sans:regular,700');


*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Open Sans', "Helvetica Neue", Helvetica, sans-serif;
	font-size: 14px;
}
textarea {
	resize: none;
	min-height: 100px;
}
.container {
	margin: 0 auto;
	width: 1000px;
	position: relative;
}
.header {
	padding-top: 40px;
	margin-bottom: 20px;
}
.title {
	font-size: 36px;
	margin-bottom: 10px;	
}
.hide {
	display: none !important;
}
.blog {
	margin-bottom: 30px;
}
.create {
		// .create__button
		&__button {
		}
}
.warning {
	& > span {
		color: red;
		font-weight: 700;
	}
}
.button,
.notification__button {
	padding: 10px;
	background-color: rgb(141, 58, 189);
	outline: none;
	cursor: pointer;
	border-radius: 8px;
	// border: 1px solid #000;
	border: none;
	transition: all .3s;
	color: #fff;
	&:hover{
		transform: scale(1.03);
		opacity: 0.8;
	}
	&:active{
		transform: scale(0.9);
	}
}
.form-box {
	& > input, textarea {
		border-radius: 5px;
		border: 1px solid rgb(133, 133, 133);
		padding: 5px 10px;
		margin-bottom: 10px;
		width: 600px;
		font-family: 'Open Sans', "Helvetica Neue", Helvetica, sans-serif;
		&:focus-visible{
			outline: none;
			border: 1px solid rgb(141, 58, 189);
		}
	}
	&__subtitle {
		display: block;
		margin-bottom: 3px;
	}
}
.form {
			// .form__button
		&__button {
		}
}

.border--violet {
	&:focus-visible{
		outline: none;
		border: 1px solid rgb(141, 58, 189) !important;
	}
}

.notification {
	position: absolute;
	top: -100%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 300px;
	height: 100px;
	border-bottom-left-radius: 20px;
	border-bottom-right-radius: 20px;
	text-align: center;
	padding: 25px;
	background-color: rgb(217, 200, 200);
	transition: all .5s;
	// .notification__text
	&__text {
		margin-bottom: 10px;
		font-size: 20px;
	}
	&__button {
		padding: 5px 15px;
		border-radius: 4px;
	}
}

.post {
	margin-bottom: 20px;
	border: 1px solid #000;
	padding: 10px 15px;
	border-radius: 15px;
	background-color: rgb(214, 214, 214);
	&-title {
		font-size: 24px;
		background-color: #333;
		display: inline-block;
		padding: 5px 10px;
		border-radius: 8px;
		cursor: pointer;
		margin-bottom: 5px;
	}
	&-text {
		font-size: 16px;
	}
}

.error {
	color: red;
	margin-top: -5px;
	margin-bottom: 10px;
}